import React from 'react';
import { Link } from 'react-router-dom';

const Register = () => {

    return(
<div class="container">

<div class="card o-hidden border-0 shadow-lg my-5">
  <div class="card-body p-0">
    {/* <!-- Nested Row within Card Body --> */}
    <div class="row">
      <div class="col-lg-5 d-none d-lg-block img-login"></div>
      <div class="col-lg-7">
        <div class="p-5">
          <div class="text-center">
            <h1 class="h4 text-gray-900 mb-4">Crea una cuenta!</h1>
          </div>
          <form class="user">
            <div class="form-group row">
              <div class="col-sm-6 mb-3 mb-sm-0">
                <input type="text" class="form-control form-control-user" id="exampleFirstName" placeholder="First Name"/>
              </div>
              <div class="col-sm-6">
                <input type="text" class="form-control form-control-user" id="exampleLastName" placeholder="Last Name"/>
              </div>
            </div>
            <div class="form-group">
              <input type="email" class="form-control form-control-user" id="exampleInputEmail" placeholder="Email Address"/>
            </div>
            <div class="form-group row">
              <div class="col-sm-6 mb-3 mb-sm-0">
                <input type="password" class="form-control form-control-user" id="exampleInputPassword" placeholder="Password"/>
              </div>
              <div class="col-sm-6">
                <input type="password" class="form-control form-control-user" id="exampleRepeatPassword" placeholder="Repeat Password"/>
              </div>
            </div>
            <Link class="btn btn-primary btn-user btn-block" to="/lista-de-certificados">Registar cuenta</Link>
           
            <hr/>
            <Link class="btn btn-google btn-user btn-block" to="/lista-de-certificados">
                            <i class="fab fa-google fa-fw"></i> Login with Google</Link>
                           
                            <Link class="btn btn-facebook btn-user btn-block" to="/lista-de-certificados">
                            <i class="fab fa-google fa-fw"></i> Login with Facebook</Link>
          </form>
          <hr/>
          <div class="text-center">
          <Link class="small" to="/forgot-password">Olvidaste tu contraseña?</Link>
          </div>
          <div class="text-center">
          <Link  class="small" to="/login">Ya tienes una cuenta? Login!</Link>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>

</div>

    );
};
export default Register;
